<template>
  <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="580px" class="cancle-google-dialog">
      <div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="220px" v-if="googleFormVisible">
          <el-form-item :label="$t('user.bindGoogleVerify.googleVerifycode')" prop="pass_double">
            <el-input v-model.trim="ruleForm.pass_double"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"  @click="phoneSubmit('ruleForm')">{{ $t('user.bindGoogleVerify.unbind') }}</el-button>
            <div class="forget-code">{{ $t('user.bindGoogleVerify.forgetVeriycode') }}<span @click="handleReset">{{ $t('user.bindGoogleVerify.reVerify') }}</span></div>
          </el-form-item>
        </el-form>
        <el-form :model="resetForm" :rules="resetRules" ref="resetForm" label-width="100px" v-else>
          <el-form-item :label="$t('user.bindGoogleVerify.tradePwd')" prop="pwdtrade">
            <el-input v-model.trim="resetForm.pwdtrade" type="password"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-button type="primary"  @click="resetSubmit('resetForm')">{{ $t('dialog.sure') }}</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
</template>

<script>
  import { cancelGoogleAuth, reset_double_verify } from '@/api/user'
  import md5 from 'js-md5'
  export default {
    props: ['dialogVisible'],
    data() {
      return {
        googleFormVisible: true,
        googleInfo: {},
        ruleForm: {
          pass_double: null
        },
        title: this.$t('user.bindGoogleVerify.bindTitle'),
        resetForm: {
          pwdtrade: null
        },
        rules: {
          pass_double: [
            { required: true, message: this.$t('errorTips.noGoogleCode'), trigger: 'blur' }
          ]
        },
        resetRules: {
          pwdtrade: [
            { required: true, message: this.$t('errorTips.withoutPwd'), trigger: 'blur' }
          ]
        }
      }
    },
    computed: {
    },
    methods: {
      handleCloseDialog() {
        this.$emit('closeDialog', 'cancleGoogleCentifyDialog')
      },
      handleReset() {
        this.googleFormVisible = false
        this.title = this.$t('user.bindGoogleVerify.reset')
      },
      phoneSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.ruleForm }
            cancelGoogleAuth(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('user.bindGoogleVerify.unbindSuccess'))
              this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      resetSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.resetForm }
            data.pwdtrade = md5(data.pwdtrade)
            reset_double_verify(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('user.bindGoogleVerify.reSetSuccess'))
              this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
  
    }
  }
</script>

<style lang="less" scoped>
  .cancle-google-dialog{
    .payway{
      margin-bottom: 15px;
      label{
        width: 86px;
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
    .forget-code{
      text-align: right;
      width: 290px;
      padding-top: 10px;
      // margin: 0 auto;
      span{
        color: #7d58d1;
        cursor: pointer;
      }
    }
  }
</style>
<style lang="less">
  .cancle-google-dialog{
     .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }
    .el-dialog__body {
      padding: 23px 20px 30px;
    }
    .el-input__inner,
    .el-button--primary{
      width: 290px;
    }
    .el-button--primary{
      margin-left: 0;
      margin-top: 0;
    }
    .el-form-item__content{
      line-height: normal;
    }
  }
</style>



