<template>
  <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="580px" class="safe-Centify-dialog">
      <div>
        <el-form :model="resetForm" :rules="resetRules" ref="resetForm" label-width="140px">
          <el-form-item :label="setPwd.label1" prop="new_password">
            <el-input v-model.trim="resetForm.new_password" type="password" :placeholder="$t('tradePwd.part12')"></el-input>
          </el-form-item>
          <el-form-item :label="$t('user.safeCenfity.confirm')" prop="new_confirmpwd"> 
            <el-input v-model.trim="resetForm.new_confirmpwd" type="password"></el-input>
          </el-form-item>
          <el-form-item :label="$t('translateLang.user.text5')" v-if="userInfo">
            <span class="phone">{{ userInfo.phone |crypto}}</span>
          </el-form-item>
          <el-form-item :label="$t('user.bindPhoneDialog.code')" prop="sms_verify" class="verify">
            <el-input  
              type="text" 
              auto-complete="off" 
              :placeholder="$t('user.bindPhoneDialog.codePlaceholder')" 
              v-model.trim="resetForm.sms_verify"></el-input>
            <el-button 
              class="get-verify" 
              @click="handleSendRequestSms($event)" 
              :disabled="!validCode">{{ validTip }}
            </el-button>
          </el-form-item> 
          <el-form-item>
            <el-button type="primary"  @click="setSubmit('resetForm')">{{ $t('dialog.sure') }}</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
</template>

<script>
  import { anotherGroup, checkSafetyQuestion, setPwdTradeV1 } from '@/api/user'
  import md5 from 'js-md5'
  import { sendRequestSms } from '@/api/loginModule'
  import { sixNumber, tradePwd } from '@/utils/validate'
  // import { Phone } from '@/utils/validate'

  export default {
    props: ['dialogVisible', 'tradeStatus', 'safeStatus', 'userInfo'],
    data() {
      // const validatePass = (rule, value, callback) => {
      //   if (value === '') {
      //     callback(new Error(this.$t('errorTips.withoutPwd')))
      //   } else {
      //     if (!sixNumber(value)) {
      //       // 错误提示
      //       callback(new Error(this.$t('errorTips.tradePwdError')))
      //     }
      //     callback()
      //   }
      // }
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.surePwd')))
        } else if (value !== this.resetForm.new_password) {
          callback(new Error(this.$t('errorTips.surePwdError')))
        } else {
          callback()
        }
      }
      const validatePass3 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutPwd')))
        } else {
          if (!sixNumber(value)) {
            // 错误提示
            callback(new Error(this.$t('errorTips.tradePwdError')))
          }
          if (!tradePwd(value)) {
            // 错误提示
            callback(new Error(this.$t('tradePwd.part15')))
          }
          callback()
        }
      }
      return {
        formVisible: true,
        safeInfo: {
          // question1: '',
          // question2: ''
        },
        ruleForm: {
          question_answer_1: null,
          question_answer_2: null,
          question_id_1: 0,
          question_id_2: 0
        },
        setPwd: {
          label1: this.$t('user.safeCenfity.setPwd')
        },
        resetForm: {
          new_password: '',
          new_confirmpwd: '',
          sms_verify: ''
        },
        validCode: true,
        validTip: this.$t('verifyCode.title'),
        resetRules: {
          new_password: [
            { validator: validatePass3, trigger: 'blur' },
            { required: true, message: this.$t('errorTips.withoutPwd'), trigger: 'blur' }
          ],
          new_confirmpwd: [
            { required: true, message: this.$t('errorTips.tradePwdError'), trigger: 'blur' },
            { validator: validatePass2, trigger: 'blur' }
  
          ],
          sms_verify: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ]
        },
        title: this.$t('user.safeCenfity.title'),
        rules: {
          question_answer_1: [
            { required: true, message: this.$t('user.safeCenfity.tips.part3'), trigger: 'blur' }
          ],
          question_answer_2: [
            { required: true, message: this.$t('user.safeCenfity.tips.part3'), trigger: 'blur' }
          ]
        }
      }
    },
    mounted() {
      if (this.safeStatus && this.safeStatus === '1') {
        this.initSafeQuestion()
      }
    },
    methods: {
      handleCloseDialog() {
        this.$emit('closeDialog', 'safeCentifyDialogVisible')
      },
      handleReset() {
        this.initSafeQuestion()
      },
      initSafeQuestion() {
        anotherGroup().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.$set(this.safeInfo, 'question1', res.data[0].question_detail)
          this.$set(this.safeInfo, 'question2', res.data[1].question_detail)
          this.ruleForm.question_id_1 = res.data[0].id
          this.ruleForm.question_id_2 = res.data[1].id
        }).catch(() => {
        })
      },
      formSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.ruleForm }
            checkSafetyQuestion(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('translateLang.user.text6'))
              this.formVisible = false
              if (this.tradeStatus === 0) {
                this.title = this.$t('user.safeCenfity.title')
              } else {
                this.title = this.$t('user.safeCenfity.title2')
                this.setPwd.label1 = this.$t('user.safeCenfity.newpwd')
              }
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      setSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.resetForm }
            data.password = md5(data.new_password)
            data.confirm_password = md5(data.new_confirmpwd)
            setPwdTradeV1(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$emit('change', true)
              this.$message.success(this.$t('user.safeCenfity.tips.part5'))
              this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      handleSendRequestSms(event) {
        const data = {}
        this.setTime()
        data.user_account = this.userInfo.phone
        data.request_type = 'edit'
        sendRequestSms(data).then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
        }).catch(() => {
        })
      },
      setTime() {
        let time = 60
        if (this.validCode) {
          this.validCode = false
          const _this = this
          const t = setInterval(function() {
            time--
            _this.validTip = time + _this.$t('verifyCode.second')
            if (time === 0) {
              clearInterval(t)
              _this.validTip = _this.$t('verifyCode.getAgain')
              _this.validCode = true
            }
          }, 1000)
          return t
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .safe-Centify-dialog{
    .payway{
      margin-bottom: 15px;
      label{
        width: 86px;
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    p{
      margin-left: 80px;
      margin-bottom: 10px;
    }
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
    .forget-code{
      text-align: right;
      width: 280px;
      padding-top: 10px;
      margin: 0 auto;
      span{
        color:#1fa8f6;
        cursor: pointer;
      }
    }
    .phone{
      line-height: 40px;
      font-weight: bold;
    }
    
  }
</style>
<style lang="less">
  .safe-Centify-dialog{
    .verify .el-input__inner,
    .verify .el-input{
      width: 184px !important;
    }
    .get-verify {
      width: 95px;
      margin-left: 10px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(31,168,246,.15);
      color: rgba(31, 168, 246, 0.7);
    }
    .el-button--primary{
      margin:0;
    }
  }
</style>



