          <!-- <p v-if="$i18n.locale === 'zn'"><span class="name" >所在地：</span> <span class="value">{{ userInfo.country_info['1'] }}</span></p>
          <p v-if="$i18n.locale === 'zh-hk'"><span class="name">所在地：</span> <span class="value">{{ userInfo.country_info['2'] }}</span></p>
          <p v-if="$i18n.locale === 'en'"><span class="name">所在地：</span> <span class="value">{{ userInfo.country_info['3'] }}</span></p> -->
        </div>
      </div>
    </div>
    
    <div class="container">
      <div id="" class="content-title">
      {{ $t('translateLang.user.text7') }}
    </div>
    <div class="verification">
      
      <p class="clearfix" v-if="userInfo.is_bind_phone == 0">
        <span class="name">{{ $t("user.tel") }}：</span>
        <span class="value" id="phone">&nbsp;&nbsp;{{ $t("user.unbind1") }}</span>
        <span class="description">{{ $t("user.telTips") }}</span>
        <a href="javascript:void(0)" class="phone" @click="bindPhoneDialogVisible = true">{{ $t("user.tobind") }}</a>
      </p>
      <p class="clearfix" v-else>
        <span class="name">{{ $t("user.tel") }}：</span>
        <span class="value" id="phone">&nbsp;&nbsp;{{ userInfo.phone }}</span>
        <span class="description">{{ $t("user.telTips") }}</span>

        <a href="javascript:void(0)" class="phone"  @click="handleModifyPhone">{{ $t("user.modify") }}</a>
      </p>
      <p class="clearfix" v-if="userInfo.is_bind_email == 0">
        <span class="name">{{ $t("user.email") }}：</span> 
        <span class="value" id="email">&nbsp;&nbsp;{{ $t("user.unbind1") }}</span>
        <span class="description">{{ $t("user.telTips") }}</span>
        <a href="javascript:void(0)" @click="bindEmailDialogVisible = true">{{ $t("user.tobind") }}</a>
      </p>
      <p class="clearfix" v-else>
        <span class="name">{{ $t("user.email") }}：</span> 
        <span class="value" id="email">&nbsp;&nbsp;{{ userInfo.email }}</span>
        <span class="description">{{ $t("user.telTips") }}</span>
        <a href="javascript:void(0)" @click="handleModifyEmail">{{ $t("user.modify") }}</a>
      </p>
      <!-- <p class="clearfix" v-if="userInfo.is_set_question == 0">
        <span class="name">{{ $t("user.safeQuestion") }}：</span> 
        <span class="value" id="email">&nbsp;&nbsp;{{ $t("user.unSet") }}</span>
        <span class="description">{{ $t("user.safeQuestionTips") }}</span>
        <a href="javascript:void(0)" @click="safeQuestionsDialogVisible = true">{{ $t("user.toSet") }}</a>
      </p>
      <p class="clearfix" v-else>
        <span class="name">{{ $t("user.safeQuestion") }}</span> 
        <span class="value" id="set_question">&nbsp;&nbsp;{{ $t("user.hadSet") }}</span>
        <span class="description">{{ $t("user.safeQuestionTips") }}</span>
      </p> -->
      <p class="clearfix" v-if="userInfo.is_set_double_certification == 0">
        <span class="name">{{ $t("user.googleCenfity") }}：</span> 
        <span class="value" id="email">&nbsp;&nbsp;{{ $t("user.unCentification") }}</span>
        <span class="description">{{ $t("user.googleCenfityTips") }}</span>
        <a href="javascript:void(0)" @click="handleGoogleCentify">{{ $t("user.toCentify") }}</a>
      </p>
      <p class="clearfix" v-else>
