        {{ $t('translateLang.user.text8') }}<span class="tips">&nbsp;&nbsp;&nbsp;&nbsp;{{ $t('translateLang.user.text9') }}</span>
      </div>
      <div class="info clearfix">
        <p class="clearfix" v-if="userInfo.status == 0">
          <span class="name">{{ $t('translateLang.user.text8') }}</span> 
          <span class="value" id="trade_pwd">&nbsp;&nbsp;{{ $t('translateLang.user.text10') }}</span>
          <router-link to="/user/centification">{{ $t('translateLang.user.text11') }}</router-link>
        </p>
        <p class="clearfix" v-if="userInfo.status == 1">
          <span class="name">{{ $t('translateLang.user.text8') }}</span> 
          <span class="value" id="trade_pwd">&nbsp;&nbsp;{{ $t('translateLang.user.text13') }}</span>
          <router-link to="/user/centification">{{ $t('translateLang.user.text14') }}</router-link>
        </p>
        <p class="clearfix" v-if="userInfo.status == 2">
          <span class="name">{{ $t('translateLang.user.text8') }}</span> 
          <span class="value" id="trade_pwd">&nbsp;&nbsp;{{ $t('translateLang.user.text12') }}</span>
          <router-link to="/user/centification">{{ $t('translateLang.user.text14') }}</router-link>
        </p>
        <p class="clearfix" v-if="userInfo.status == 3">
          <span class="name">{{ $t('translateLang.user.text8') }}</span> 
          <span class="value" id="trade_pwd">&nbsp;&nbsp;{{ $t('translateLang.user.text17') }}</span>
          <router-link to="/user/centification">{{ $t('translateLang.user.text14') }}</router-link>
        </p>
