</template>
<script>
  import { setToken } from '@/utils/auth'
  import Cookies from 'js-cookie'
  import { checkToken, getUserPayInfo, userInfo, uploadUserHead } from '@/api/user'
  import modifyPhoneDialog from './components/modifyPhoneDialog'
  import bindPhoneDialog from './components/bindPhoneDialog'
  import modifyEmailDialog from './components/modifyEmailDialog'
  import bindEmailDialog from './components/bindEmailDialog'
  import safeQuestionsDialog from './components/safeQuestionsDialog'
  import googleCentifyDialog from './components/googleCentifyDialog'
  import cancleGoogleCentifyDialog from './components/cancleGoogleCentifyDialog'
  import pwdModifyDialog from './components/pwdModifyDialog'
  import safeCentifyDialog from './components/safeCentifyDialog'
  import nickNameModifyDialog from './components/nickNameModifyDialog'
  import centifyByPwdDialog from './components/centifyByPwdDialog'
  import centifyByNumberDialog from './components/centifyByNumberDialog'
  import sexModifyDialog from './components/sexModifyDialog'
  
  export default {
    components: { sexModifyDialog, centifyByNumberDialog, centifyByPwdDialog, safeCentifyDialog, nickNameModifyDialog, pwdModifyDialog, modifyPhoneDialog, bindPhoneDialog, modifyEmailDialog, bindEmailDialog, safeQuestionsDialog, googleCentifyDialog, cancleGoogleCentifyDialog },
    data() {
      return {
        payTypeList: [],
        paywayDialogVisible: false,
        modifyPhoneDialogVisible: false,
        bindPhoneDialogVisible: false,
        modifyEmailDialogVisible: false,
        bindEmailDialogVisible: false,
        safeQuestionsDialogVisible: false,
        googleCentifyDialogVisible: false,
        cancleGoogleCentifyDialog: false,
        pwdModifyDialogVisible: false,
        safeCentifyDialogVisible: false,
        nickNameModifyDialogVisible: false,
        sexModifyDialogVisible: false,
        centifyWaysDialogVisible: false,
        centifyByPwdDialogVisible: false,
        centifyByNumberDialogVisible: false,
        tradeStatus: 0, // 未设置交易密码状态
        requireType: 0,
        payIdCurrent: 0,
        payWayTitle: this.$t('user.addPayways'),
        modifyPayInfo: {},
        userInfo: {},
        headImgFile: null,
        check_options: '1',
        isEn: Cookies.get('language') === 'en'
      }
    },
    created() {
      this.initUserInfo()
      this.getUserPayTypeList()
    },
    methods: {
      formatName(val) {
        if (!val || val.length <= 4) return val
        const result = val.substr(0, 3) + '***' + val.substr(-4, 4)
        return result
      },
      handleCentifyWays() {
        if (this.check_options === '1') {
          this.$router.push('centifyByPwd')
          // this.centifyByPwdDialogVisible = true
        } else {
          this.$router.push('centifyByNumber')
          // this.centifyByNumberDialogVisible = true
        }

        this.centifyWaysDialogVisible = false
      },
      IsSetTradePwd() {
        if (this.userInfo.is_pwdtrade_exists === '0') {
          this.$message.error(this.$t('user.tips.unsetPwd'))
          return false
        }
        return true
      },
      handleModifyEmail() {
        if (this.userInfo.status !== 1) {
          this.$message.error(this.$t('translateLang.tradeDetail.text71'))
        } else {
          this.modifyEmailDialogVisible = true
        }
      },
      handleSetTradePwd() {
        if (this.userInfo.is_bind_phone !== 1) {
          this.$message.error(this.$t('errorTips.bindPhoneFist'))
        } else {
          this.safeCentifyDialogVisible = true
        }
      },
      getUserInfo() {
        checkToken().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          // console.log(res.data)
          setToken(res.data.token)
          const userInfo = JSON.stringify({
            'uid': res.data.id,
            'token': res.data.token,
            'user_account': res.data.nickname, // 登录帐号
            'nickname': res.data.nickname,
            'head': res.data.head,
            'email': res.data.email,
            'phone': res.data.phone
          })
          window.localStorage.removeItem('link_userInfo')
          window.localStorage.setItem('link_userInfo', userInfo)
          const userinfoObj = JSON.parse(userInfo)
          this.userInfo.head = userinfoObj.head
          this.userInfo.nickname = userinfoObj.nickname
          this.userInfo.phone = userinfoObj.phone
          this.userInfo.email = userinfoObj.email
          this.$store.commit('SET_USER_AVATAR', userinfoObj.head)
          this.$store.commit('SET_USER_NICKNAME', userinfoObj.nickname)
          this.$store.commit('SET_USER_PHONE', this.formatName(userinfoObj.phone))
          this.$store.commit('SET_USER_EMAIL', this.formatName(userinfoObj.email))
          // window.location.reload()
        }).catch(() => {
        })
      },
      handleModifyPhone() {
        if (this.userInfo.status !== 1) {
          this.$message.error(this.$t('translateLang.tradeDetail.text71'))
        } else {
          this.modifyPhoneDialogVisible = true
        }
      },
      getUserPayTypeList() {
      },
      modifyPayway(pay_id) {
        this.initUserPayInfo(pay_id)
        this.requireType = 1
        this.paywayDialogVisible = true
        this.payIdCurrent = pay_id
        this.payWayTitle = this.$t('user.modifyPayways')
      },
      addPayWay() {
        if (this.userInfo.is_pwdtrade_exists !== '0') {
          this.requireType = 0
          this.paywayDialogVisible = true
          this.payWayTitle = this.$t('user.addPayways')
          this.payIdCurrent = 0
        } else {
          this.$message.error(this.$t('user.tips.unsetPwd'))
        }
      },
      initUserPayInfo(pay_id) {
        const data = {}
        data.pay_id = pay_id
        getUserPayInfo(data).then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.modifyPayInfo = res.data
        }).catch(() => {
        })
      },
      initUserInfo() {
        userInfo().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          console.log(res.data)
          this.userInfo = res.data
        }).catch(() => {
        })
      },
      handleChange(file, fileList, str) {
        var reader = new FileReader()
        reader.readAsDataURL(file.raw)
        const _this = this
        reader.onload = function() {
          _this.headImgFile = reader.result
          _this.handlePhotoModify()
        }
        this.userInfo.head_img = URL.createObjectURL(file.raw)
      },
      handlePhotoModify() {
        const data = {}
        data.head_img = this.headImgFile
        uploadUserHead(data).then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.$message.success(this.$t('user.tips.modifyphotoSuccess'))
          this.getUserInfo()
        }).catch(() => {
        })
      },
      closeDialog(str) {
        this[str] = false
        this.getUserPayTypeList()
        this.initUserInfo()
      },
      handleGoogleCentify() {
        this.googleCentifyDialogVisible = true
        // if(this.userInfo.is_pwdtrade_exists != '0') {
        //   this.googleCentifyDialogVisible = true
        // } else {
        //   this.$message.error('请先设置交易密码！')
        // }
      }
    }
  }
</script>
