<style lang="less">
  .person{
    .el-dialog__body {
      padding: 23px 20px 30px;
    }
    .el-input__inner,
    .el-button--primary{
      width: 290px;
    }
    .el-button--primary{
      margin-left: 180px;
      margin-top: 15px;
    }
    .el-form-item__content{
      line-height: normal;
    }
    .en-content{
      .el-dialog{
        width: 600px !important;
      }
    }
  }
</style>



