<template>
  <el-dialog
      :title="$t('user.modifyPhoneDialog.title')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="580px" class="mdphone-dialog">
      <el-form :model="phoneForm" :rules="phoneFormRules" ref="phoneForm" label-width="180px">
        <el-form-item :label="$t('translateLang.user.text2')" prop="card_num">
          <el-input v-model.trim="phoneForm.card_num" :label="$t('translateLang.user.text3')"></el-input>
        </el-form-item>

        <el-form-item class="phone-account" prop="user_account" :label="$t('form.phone')">
          <el-select v-model="phoneForm.code_area" class="phone-code-area">
            <el-option :value="'+' + item.code_area" v-for="item in smsCodeList" :key="item.code_id"><div class="clearfix code-area"><div class="pull-left area">{{ item.nation_cn }}</div><div class="pull-right">+{{ item.code_area }}</div></div></el-option>
          </el-select>
          <el-input v-model.trim="phoneForm.user_account" class="phone-num"></el-input>
        </el-form-item>
        
        <!-- <el-form-item :label="$t('form.tradePwd')" prop="pwdtrade">
          <el-input v-model.trim="phoneForm.pwdtrade" type="password" :placeholder="$t('form.tradePwdPlaceholder')"></el-input>
        </el-form-item> -->
        <!-- <el-form-item :label="$t('form.phone')" prop="user_account">
          <el-input v-model.trim="phoneForm.user_account" :placeholder="$t('form.phonePlaceholder')"></el-input>
        </el-form-item> -->
        <el-form-item :label="$t('form.verifyCode')" prop="sms_verify" class="verify">
          <el-input type="text" auto-complete="off" :placeholder="$t('form.verifyCodePlaceholder')" v-model.trim="phoneForm.sms_verify"></el-input><el-button class="get-verify" @click="handleSendRequestSms($event)" :disabled="!validCode">{{ validTip }}</el-button>
        </el-form-item>
        <div class="btn-group appeal-btn">
          <el-button type="primary"  @click="phoneSubmit('phoneForm')">{{ $t('dialog.modify') }}</el-button>
        </div>
      </el-form>
    </el-dialog>
</template>

<script>
  import { sendRequestSms, getInternationalSmsCode } from '@/api/loginModule'
  import { phoneORemailAuth } from '@/api/user'
  import { Phone } from '@/utils/validate'
  // import md5 from 'js-md5'
  export default {
    props: ['dialogVisible'],
    data() {
      // const validateTradePwd = (rule, value, callback) => {
      //   if (!sixNumber(value)) {
      //     callback(new Error(this.$t('errorTips.tradePwdError')))
      //   }
      //   callback()
      // }
      const validatePhone = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutPhone')))
        } else {
          if (Phone(value)) {
            callback()
          } else {
            callback(new Error(this.$t('errorTips.phoneFormat')))
          }
        }
      }
      return {
        phoneForm: {
          sms_verify: '',
          user_account: '',
          code_area: '',
          card_num: '',
          require_type: 2
        },
        smsCodeList: [],
        phoneFormRules: {
          sms_verify: [
            { required: true, message: this.$t('errorTips.withoutVerifyCode'), trigger: 'blur' }
          ],
          user_account: [
            { required: true, validator: validatePhone, trigger: 'blur' }
          ],
          card_num: [
            { required: true, message: this.$t('translateLang.user.text4'), trigger: 'blur' }
          ]
        },
        validCode: true,
        validTip: this.$t('verifyCode.title')
      }
    },
    computed: {
    },
    created() {
      this.handleGetInternationalSmsCode()
    },
    methods: {
      handleGetInternationalSmsCode() {
        getInternationalSmsCode().then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.smsCodeList = res.data
          this.smsCodeList.unshift(res.data[res.data.length - 1])
          this.smsCodeList.pop()
          // const length = res.data.length - 1
          this.phoneForm.code_area = '+' + this.smsCodeList[0].code_area
        }).catch(() => {
        })
      },
      handleCloseDialog() {
        this.$emit('closeDialog', 'modifyPhoneDialogVisible')
      },
      phoneSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.phoneForm }
            // data.pwdtrade = md5(data.pwdtrade)
            if (this[formName].code_area) {
              data.code_area = this[formName].code_area.substring(1)
            }
            phoneORemailAuth(data).then(res => {
              console.log('res', res)
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success('修改成功！')
              this.$emit('getUserInfo')
              this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      handleSendRequestSms(event) {
        if (!this.phoneForm.user_account) {
          this.$message.error(this.$t('errorTips.withoutPhone'))
          return false
        }
        if (!Phone(this.phoneForm.user_account)) {
          this.$message.error(this.$t('errorTips.phoneFormat'))
          return false
        }
        const data = {}
        data.user_account = this.phoneForm.user_account
        data.request_type = 'edit'
        data.code_area = this.phoneForm.code_area.substring(1)
        sendRequestSms(data).then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.setTime()
        }).catch(() => {
        })
      },
      setTime() {
        let time = 60
        if (this.validCode) {
          this.validCode = false
          const _this = this
          const t = setInterval(function() {
            time--
            _this.validTip = time + _this.$t('verifyCode.second')
            if (time === 0) {
              clearInterval(t)
              _this.validTip = _this.$t('verifyCode.getAgain')
              _this.validCode = true
            }
          }, 1000)
          return t
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .mdphone-dialog{
    .phone-account{
      // overflow: hidden;
      /deep/.el-select .el-input__inner{
        width: 100px;
      }
      .area{
        width: 300px !important;
      }
      /deep/ .el-input--suffix .el-input__inner{
        height: 37px;
      }
      .phone-num{
        /deep/ .el-input__inner{
          padding-left: 120px;
        }
      }
      .phone-code-area{
        position: absolute;
        z-index: 4;
        top: 1px;
        left: 1px;
        border-right: 1px solid #dcdfe6;
        /deep/ .el-input__inner{
          border: none;
          left: 0;
        }
        /deep/ .el-input__validateIcon{
          display: none;
        }
      }
      
      /deep/ .el-input__inner:focus{
        border-color: #dcdfe6;
      }
      
    }
    .payway{
      margin-bottom: 15px;
      label{
        width: 86px;
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
    .tips{
      color: #b0b1b7;
      margin-left: 90px;
      margin-bottom: 10px;
    }
  }
</style>
<style lang="less">
  .mdphone-dialog{
    .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }
  }
</style>
<style lang="less">
  .mdphone-dialog{
    .verify .el-input__inner,
    .verify .el-input{
      width: 184px;
    }
    .get-verify {
      width: 95px;
      margin-left: 10px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(31,168,246,.15);
      color: rgba(31, 168, 246, 0.7);
    }
    .sure-change{
      padding: 12px 100px;
    }
    .code-area{
      width: 300px !important;
    }
  }
</style>


