<template>
  <el-dialog
      :title="$t('tradePwd.part5')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="520px" class="mdphone-dialog">
      <!-- <el-steps :active="active" finish-status="success">
          <el-step :title="$t('tradePwd.part6')"></el-step>
          <el-step :title="$t('tradePwd.part5')"></el-step>
          <el-step :title="$t('tradePwd.part7')"></el-step>
      </el-steps> -->
      <steps-bar 
        :item="item"
        :index="active"
        bgColor="#0bc988"
        disableColor="#e4e4ea"
        activeColor="#0bc988">
      </steps-bar>
      <el-form :model="form" :rules="formRules" ref="form" label-width="80px" v-if="active == 1">
        <el-form-item label="" prop="old_pwdtrade" class="old-pwdtrade-container">
          <p style="line-height:50px;">{{$t('tradePwd.oldTradePwd')}}</p>
          <paybox @payPwd="(val)=>form.old_pwdtrade=val"></paybox>
          <!-- <el-input v-model.trim="form.old_pwdtrade" type="password" :placeholder="" class="old-pwd"></el-input> -->
        </el-form-item>
        <div class="old-pwdtrade-btn">
          <el-button style="margin-top: 5px; margin-left: 0px;" @click="nextSubmit('form')" type="primary" >{{ $t('tradePwd.part8') }}</el-button>
        </div>
      </el-form>

      <el-form :model="resetForm" :rules="resetRules" ref="resetForm" label-width="180px" v-if="active == 2" style="margin-top: 20px;">
        <el-form-item :label="$t('tradePwd.part11')+':'"  prop="new_password">
          <el-input v-model.trim="resetForm.new_password" type="password" :placeholder="$t('tradePwd.part12')"></el-input>
        </el-form-item>
        <el-form-item :label="$t('tradePwd.part13')+':'" prop="new_confirmpwd">
          <el-input v-model.trim="resetForm.new_confirmpwd" type="password" :placeholder="$t('tradePwd.part14')"></el-input>
        </el-form-item>
        <div class="btn-group appeal-btn">
          <el-button type="primary"  @click="resetSubmit('resetForm')" style="margin-left:180px;">{{ $t('dialog.submit') }}</el-button>
        </div>
      </el-form>

      <div class="c-green centify-success" v-if="active == 3"><img src="static/images/right.png" alt="">{{ $t('tradePwd.part10') }}</div>
      
    </el-dialog>
</template>

<script>
  import StepsBar from '@/components/StepsBar'
  import Paybox from '@/components/Paybox'

  import { checkUserAuth, resetPwdTrade } from '@/api/user'
  import { sixNumber } from '@/utils/validate'
  import md5 from 'js-md5'
  export default {
    components: { StepsBar, Paybox },
    props: ['dialogVisible'],
    data() {
      const validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.withoutPwd')))
        } else {
          if (!sixNumber(value)) {
            // 错误提示
            callback(new Error(this.$t('errorTips.tradePwdError')))
          }
          callback()
        }
      }
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(this.$t('errorTips.surePwd')))
        } else if (value !== this.resetForm.new_password) {
          callback(new Error(this.$t('errorTips.surePwdError')))
        } else {
          callback()
        }
      }
      return {
        active: 1,
        item: [ // 步骤条
          {
            txt: this.$t('tradePwd.part6'),
            color: `#ccc`,
            index: 1
          },
          {
            txt: this.$t('tradePwd.part5'),
            color: `#ccc`,
            index: 2
          },
          {
            txt: this.$t('tradePwd.part7'),
            color: `#ccc`,
            index: 3
          }
        ],
        resetForm: {
          new_password: '',
          new_confirmpwd: ''
        },
        resetRules: {
          new_password: [
            { validator: validatePass, trigger: 'blur' },
            { required: true, message: this.$t('errorTips.withoutPwd'), trigger: 'blur' }
          ],
          new_confirmpwd: [
            { validator: validatePass2, trigger: 'blur' },
            { required: true, message: this.$t('errorTips.tradePwdError'), trigger: 'blur' }
          ]
        },
        form: {
          old_pwdtrade: ''
        },
        formRules: {
          old_pwdtrade: [
            { validator: validatePass, trigger: 'blur' },
            { required: true, message: this.$t('errorTips.withoutPwd'), trigger: 'blur' }
          ]
        }
  
      }
    },
    computed: {
    },
    created() {
  
    },
    methods: {
      nextSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.form }
            data.check_options = 1
            data.old_pwdtrade = md5(data.old_pwdtrade)
            checkUserAuth(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }

              this.active++
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      handleCloseDialog() {
        this.$emit('closeDialog', 'centifyByPwdDialogVisible')
      },
      resetSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.resetForm }
            data.new_password = md5(data.new_password)
            data.new_confirmpwd = md5(data.new_confirmpwd)
            resetPwdTrade(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.active++
              // this.$message.success(this.$t('user.safeCenfity.tips.part5'))
              // this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .mdphone-dialog{
    .steps-bar{
      margin-bottom: 30px;
    }
    .payway{
      margin-bottom: 15px;
      label{
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .old-pwd{
      margin-left: -60px;
      margin-top: 30px;
    }
  
    .old-pwdtrade-btn{
      text-align: center;
      margin-left: 0;
    }

    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
    .centify-success{
      margin: 0 auto;
      font-size: 16px;
      font-weight: bold;
      margin-left: 100px;
      margin-top: 30px;
      line-height: 43px;
      img{
        float: left;
      }
    }
    .tips{
      color: #b0b1b7;
      margin-left: 90px;
      margin-bottom: 10px;
    }

  }
</style>
<style lang="less">
  .mdphone-dialog{
    .el-step__title.is-success{
      color: #02c56c !important;
    }
    .el-step__head.is-success{
      color: #02c56c;
      border-color: #02c56c;
    }
    .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }

  }
</style>
<style lang="less">
  .mdphone-dialog{
    .verify .el-input__inner,
    .verify .el-input{
      width: 184px;
    }
    .get-verify {
      width: 95px;
      margin-left: 10px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(31,168,246,.15);
      color: rgba(31, 168, 246, 0.7);
    }
    .sure-change{
      padding: 12px 100px;
    }
    .el-step__title{
      font-size: 14px;
    }
  }
</style>


