<template>
  <el-dialog
      :title="$t('user.setSafeQuestion.title')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="580px" class="question-dialog">
      <div>
        <div class="tips">{{ $t('user.setSafeQuestion.tips') }}</div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="180px">
          <el-form-item :label="$t('user.setSafeQuestion.question')" prop="question_id_1">
            <el-select v-model="ruleForm.question_id_1">
              <el-option :label="item.question_detail" :value="item.id" v-for="item in questions.question_1" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="$t('user.setSafeQuestion.answer')" prop="question_answer_1">
            <el-input v-model.trim="ruleForm.question_answer_1" ></el-input>
          </el-form-item>
          <el-form-item :label="$t('user.setSafeQuestion.question')" prop="question_id_2">
            <el-select v-model="ruleForm.question_id_2">
              <el-option :label="item.question_detail" :value="item.id" v-for="item in questions.question_2" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="$t('user.setSafeQuestion.answer')" prop="question_answer_2">
            <el-input v-model.trim="ruleForm.question_answer_2" ></el-input>
          </el-form-item>
          <el-form-item :label="$t('user.setSafeQuestion.question')" prop="question_id_3">
            <el-select v-model="ruleForm.question_id_3">
              <el-option :label="item.question_detail" :value="item.id" v-for="item in questions.question_3" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="$t('user.setSafeQuestion.answer')" prop="question_answer_3">
            <el-input v-model.trim="ruleForm.question_answer_3"></el-input>
          </el-form-item>
          <div class="btn-group appeal-btn">
            <el-button type="primary"  @click="phoneSubmit('ruleForm')">{{ $t('dialog.submit') }}</el-button>
          </div>
        </el-form>
      </div>
    </el-dialog>
</template>

<script>
  import { safeQuestion, submitSafeQuestion } from '@/api/user'
  
  export default {
    props: ['dialogVisible'],
    data() {
      return {
        ruleForm: {
          question_id_1: null,
          question_id_2: null,
          question_id_3: null,
          question_answer_1: '',
          question_answer_2: '',
          question_answer_3: ''
        },
        orderData: {
          question_order_id_1: null,
          question_order_id_2: null,
          question_order_id_3: null
        },
        rules: {
          question_id_1: [
            { required: true, message: this.$t('user.setSafeQuestion.tips1'), trigger: 'change' }
          ],
          question_answer_1: [
            { required: true, message: this.$t('user.setSafeQuestion.tips2'), trigger: 'blur' }
          ],
          question_id_2: [
            { required: true, message: this.$t('user.setSafeQuestion.tips1'), trigger: 'change' }
          ],
          question_answer_2: [
            { required: true, message: this.$t('user.setSafeQuestion.tips2'), trigger: 'blur' }
          ],
          question_id_3: [
            { required: true, message: this.$t('user.setSafeQuestion.tips1'), trigger: 'change' }
          ],
          question_answer_3: [
            { required: true, message: this.$t('user.setSafeQuestion.tips2'), trigger: 'blur' }
          ]
        },
        validCode: true,
        validTip: this.$t('verifyCode.title'),
        questions: {}
      }
    },
    computed: {
    },
    created() {
      this.initQuestion()
    },
    methods: {
      initQuestion() {
        safeQuestion().then(res => {
          console.log('安全问题', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.questions = res.data
          this.orderData.question_order_id_1 = res.data.question_1[0].order_id
          this.orderData.question_order_id_2 = res.data.question_2[0].order_id
          this.orderData.question_order_id_3 = res.data.question_3[0].order_id
        }).catch(() => {
        })
      },
      handleCloseDialog() {
        this.$emit('closeDialog', 'safeQuestionsDialogVisible')
      },
      phoneSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.orderData)
            const data = { ...this.ruleForm, ...this.orderData }
            submitSafeQuestion(data).then(res => {
              console.log('安全问题', res)
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('user.setSafeQuestion.tips3'))
              window.location.reload()
              this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
  
    }
  }
</script>

<style lang="less" scoped>
  .question-dialog{
    .payway{
      margin-bottom: 15px;
      label{
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
    .tips{
      color: #b0b1b7;
      margin-left: 20px;
      margin-bottom: 10px;
    }
    .safe-questions{
      padding: 0px 60px 20px;
      line-height: 35px;
      .tips{
        color: red;
      }
      .dash-hr{
        border-bottom: dashed 1px #e5e2e9;
        margin: 10px 0;
      }
    }
  }
</style>
<style lang="less">
  .question-dialog{
    .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }
    .el-dialog__body {
      padding: 23px 20px 30px;
    }
    .el-input__inner,
    .el-button--primary{
      width: 290px;
    }
    .el-button--primary{
      margin-left: 90px;
      margin-top: 15px;
    }
    .el-form-item__content{
      line-height: normal;
    }
  }
</style>


